}
.DR-dynamic-terms-container {
    margin-right: .5em;
    height: 32px;
    min-width: 36px;
    text-align: right;
}
.DR-payment-description {
    padding: .5em;
    border-bottom: 1px solid rgba(0,0,0,.26);
