}
.DR-title {
    font-weight: bold;
    font-size: 1.5rem;
    margin-bottom: 10px;
}
.DR-payment-method-name-and-logos-container {
    justify-content: space-between;
    width: 100%;
}
.DR-payment-method-name-and-logo {
    width: 65%;
}
.DR-payment-method-secondary-logo-container {
    width: 30%;
    margin-right: 1em;
    display: flex;
    justify-content: flex-end;
}
.DR-pay-button, .DR-button-secondary {
    margin: .3rem
}
.DR-prevent-click {
    pointer-events: none;
}
.DR-offline-refund-success {
    align-items: center;
    justify-content: center;
