}
.DR-offline-refund-button {
    padding: 1em 2em;
    text-transform: uppercase;
    background: transparent;
    border: 2px solid currentColor;
    color: currentColor;
    border-radius: 4px;
    box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, .1);
}
.DR-offline-refund-fields {
    padding-bottom: 8px;
}
.DR-offline-refund-parent {
