}
.DR-stored-remove {
    border-radius: .25rem;
    padding: 4px;
}
.DR-disclosure-title-container {
    display: flex;
    flex-direction: row;
    margin: 0 0.5rem;
}
.DR-disclosure-title {
    margin-right: 0.5rem;
}
.DR-disclosure-select {
