}
.DR-form {
    padding: 1em;
    display: grid;
}
.DR-taxExemption-container {
    display: grid;
}
.DR-address-container {
    display: grid;
    grid-template-columns: repeat(2, 50%);
}
.DR-address-row {
    padding-right: 0.4rem;
    padding-left: 0.4rem;
    margin-bottom: 1.0em;
}
.DR-form input[type='text'], .DR-form input[type='file'], .DR-form input[type='date'], .DR-form select {
    display: block;
    height: 3.5em;
    padding: .75em;
    border: 1px solid rgba(0,0,0,.26);
    box-sizing: border-box;
}
.DR-address-item, .DR-shipping-choice {
    width: 100%;
}
.DR-addressOne-row {
    padding: 0.375em;
    margin-bottom: 1.0em;
