}
.custom-select-container {
    position: relative;
    box-sizing: border-box;
}
.custom-select-container * {
    box-sizing: border-box;
}
.custom-select-container.is-disabled {
    opacity: .333;
}
.DR-invoice-attribute-container .custom-select-opener,
.DR-invoice-attribute-container .custom-select-panel {
    background-color: #fff;
    border: 1px solid rgba(0,0,0,.26);
}
.DR-invoice-attribute-container .custom-select-opener {
    border-bottom: none;
    padding: 0.5em;
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    width: 100%;
    outline-color: transparent;
}
.DR-invoice-attribute-container .custom-select-opener::after {
    content:"\A";
    width: 0;
    height: 0;
    border-left: .5em solid transparent;
    border-right:.5em solid transparent;
    border-top: .5em solid currentColor;
    margin: .5em .25em;
    float: right;
}
.DR-invoice-attribute-container .custom-select-container select {
    visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
}
.DR-invoice-attribute-container .custom-select-panel {
    border-top:none;
    max-height: 0;
    overflow: hidden;
    position: absolute;
    top: 100%;
    z-index: 1;
    width: 100%;
    cursor: pointer;
}
.DR-invoice-attribute-container .custom-select-container.is-open .custom-select-panel {
    max-height: 200px;
    overflow-y: auto
}
.DR-invoice-attribute-container .custom-select-option {
    padding: 0.5em;
}
.DR-invoice-attribute-container .custom-select-option.is-selected::after {
    content: "✔";
    padding-left: 0.5em;
    margin: auto 0;
}
.DR-invoice-attribute-container .custom-select-container {
    display:flex;
    flex-direction: row;
}
.DR-invoice-attribute-container .custom-select-img {
